<template>
  <div class="search-labels-detail">
    <div class="city">
      <p></p>
      <p><span class="icon-aui-icon-location iconfont"></span> 热门城市:</p>
      <div class="cities">
        <template v-for="item in houselabels.cities">
          <span
            @click="changeCity(item)"
            :class="['city-item', item.cid == cid ? 'selected' : '']"
            :key="item.cid"
            >{{ item.cityname }}</span
          >
        </template>
      </div>

      <div class="region" v-if="regions.length">
        <template v-for="item in regions">
          <span
            :class="[
              'region-item',
              item.regionid == regionId ? 'selected' : '',
            ]"
            :key="item.regionid"
            @click="regionChange(item)"
          >
            {{ item.region_name }}</span
          >
        </template>
      </div>
      <p><span class="iconfont icon-Dollar"></span>租金价格：</p>
      <div class="rent_price">
        <el-radio-group v-model="houseprice">
          <template v-for="item in houselabels.housePrice">
            <el-radio :key="item.priceid" :label="item.priceid"
              >¥ {{ item.min }}-{{ item.max }}</el-radio
            >
          </template>
        </el-radio-group>
      </div>
      <p><span class="iconfont icon-project1"></span>房屋面积：</p>
      <div class="rent_price">
        <el-radio-group v-model="square">
          <template v-for="item in houselabels.square">
            <el-radio :key="item.squareid" :label="item.squareid">
              {{ item.min }}-{{ item.max }} ㎡</el-radio
            >
          </template>
        </el-radio-group>
      </div>
      <p><span class="iconfont icon-city"></span>房屋布局：</p>
      <div class="rent_price">
        <el-radio-group v-model="layout">
          <template v-for="item in houselabels.houselayout">
            <el-radio :key="item.layoutid" :label="item.layoutid">
              {{ item.layout }}</el-radio
            >
          </template>
        </el-radio-group>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  watch: {
    "houselabels.cities": {
      deep: true,
      immediate: true,
      handler(val) {
        if (val.length) {
          this.cid = val[0]?.cid;
          this.regionId = val[0]?.cityregions[0]?.regionid;
        }
      },
    },
  },
  computed: {
    ...mapGetters(["houselabels"]),
    regions() {
      return (
        (this.cid &&
          this.houselabels?.cities?.find((item) => item.cid == this.cid)
            ?.cityregions) ||
        []
      );
    },
  },
  data() {
    return {
      cid: "",
      regionId: "",
      houseprice: "",
      square: "",
      layout: "",
    };
  },
  methods: {
    regionChange(val) {
      this.regionId = val.regionid;
    },
    changeCity(val) {
      this.cid = val.cid;
      this.regionId = this.houselabels.cities.find(
        (item) => item.cid == val.cid
      ).cityregions[0]?.regionid;
    },
  },
};
</script>

<style lang="scss" scoped>
.search-labels-detail {
  padding: 10px 20px;
  .city {
    text-align: left;
    .cities {
      display: flex;
      flex-wrap: nowrap;
      @at-root .city-item,
        .region-item,
        .rent_price {
        display: inline-block;
        padding: 3px 5px;
        margin: 3px 5px;
        border-radius: 2px;
        cursor: pointer;
        &.selected {
          background: #00d7c6;
          color: white;
        }
      }
    }
  }
  .iconfont {
    font-size: 24px;
  }
}
</style>
<style lang="scss">
.search-labels-detail {
  .el-radio__inner {
    border-radius: 0;
  }
  .el-radio {
    margin: 5px 30px 5px 0;
  }
}
</style>
